import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      name: 'Home',
      component: Main
    },
    {
      path: '/:chart',
      name: 'Home',
      component: Main
    }
  ]
})



